.textInputInventory {
    display: flex;
    flex-direction: column;
    padding: 0px 0px 16px 12px;
    width: 100%;
    height: 100%;
    max-width: 1312px;
    align-items: flex-start;
    gap: 8px;
}

.TextInput{
    width: 100%;
    max-width: 642px;
    height: 78;
    display: flex;
    gap: 8px;
    display: flex;
    flex-direction: column;
}

.TextInput label{
    color: var(--grey-scale-700, #3F527A);
    /* Small 1 */
    font-family: Sarabun;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 157.143% */
    padding-inline-start: 24px;

    margin-bottom: 0px;
}

.InputWithIcon{
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: center;
}

.icon{
    color: #A5B7DA;
}

.styled-input{
    display: flex;
    padding: 12px 16px;
    align-items: flex-start;
    gap: 12px;
    align-self: stretch;

    border-radius: 8px;
    border: 1px solid var(--grey-scale-400, #C9D4F1);
    background: #FFF;

    color: var(--grey-scale-800, #2A3959);
    font-feature-settings: 'liga' off;

    /* Body 2 */
    font-family: Sarabun;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px; /* 150% */
}